.movie {
  max-width: 364px;
  position: relative;
  border-radius: 6px;
  box-shadow: 0px 4px 30px rgb(0 0 0 / 8%);
}

.movie__photo {
  display: block;
  width: 100%;
  height: 203px;
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
}

.movie__sign {
  align-items: center;
  display: flex;
  justify-content: space-between;
  margin: 18px 25px 13px 16px;
}

.movie__title {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 500;
  font-size: 13px;
  line-height: 16px;
  margin: 0;
}

.movie__like {
  width: 14px;
  height: 12px;
  background-image: url(../../../images/like_active.svg);
  background-repeat: no-repeat;
  background-size: cover;
  transition: opacity .5s ease-in-out;
  margin: 0;
  padding: 0;
  border: none;
}

.movie__like:hover {
  cursor: pointer;
  opacity: 0.6;
}

.movie__duration {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  font-size: 11px;
  line-height: 13px;
  color: #A0A0A0;
  padding: 0 0 16px 16px;
  margin: 0;
}
