.footer {
  padding: 74px 70px 20px;
}

.footer__sign {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  font-size: 13px;
  line-height: 16px;
  text-align: center;
  color: #A0A0A0;
  margin: 0 0 20px 0;
  padding: 0 0 20px 0;
  border-bottom: 1px solid #E8E8E8;
}

.footer__container {
  display: flex;
  justify-content: space-between;
}

.footer__copyright {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  font-size: 13px;
  line-height: 16px;
  margin: 0;
}

.footer__links {
  display: flex;
  justify-content: space-between;
  gap: 20px;
}

.footer__link {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  font-size: 13px;
  line-height: 16px;
  margin: 0;
  text-decoration: none;
  color: black;
  transition: opacity .5s ease-in-out;
}

.footer__link:hover {
  opacity: 0.5;
  cursor: pointer;
}
